body {
  margin: 0;
  padding: 0;
}
button #searchButton{
  padding:0px;
}
button{
  background: none;
  border: none;
  padding : 10px;
}
.NavBar{
  background: #ffffff;
  min-height: 70px;
  padding-top: 15px;
}
.NavBar .SidebarButton{
  display:inline-block;
}
#myInput{
  max-width: 92%;
  display: inline-block;
}
.NavBar .ProfileButton{
  display:inline-block; 
  float: right;
}
.ProfileButton i{
  font-size:25px;
  color: #606060;
}
#logo{
  max-width: 80px;
  margin-left: 20px;
}
#sidebarCollapse i{
  font-size: 25px;
}
#VideoThumbnail{
  width: 250px;
  height: 150px;
}
#VideoTitle{
  max-width: 250px;
  margin-top: 5px;
}
.VideoComponent{
  max-width: 250px;
  word-wrap: break-word;
}
#ChannelName{
color:grey;
}
#ViewsCount{
  color:grey;
}
#UploadTime{
color:grey;
}
.VideoCategory{
display: inline-block;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

